<template>
    <div>
        <input type="text" v-model="state.btitle" placeholder="제목">
        <input type="text" v-model="state.bcontent" placeholder="내용">
        <input type="button" @click="handleInsert" value="작성하기" />
        
    </div>
</template>

<script>
import axios from 'axios';
import { reactive } from '@vue/reactivity'
import {useRouter} from 'vue-router';
export default {
    setup () {
        const router = useRouter();
        const state = reactive({
            btitle:'',
            bcontent:''
        })

        const handleInsert = async()=>{
            const url = `/ROOT/api/board/insert`;
            const headers = {"Content-Type": "application/json"};
            const body = {
                btitle: state.btitle,
                bcontent: state.bcontent
            }
            const response = await axios.post(url, body, {headers})
            console.log(response)
            if(response.data.status ===200){
                alert("등록완료")
                router.push({name:"Board"});
            }
        }

        return {
            state,
            handleInsert
        }
    }
}
</script>

